.landing-page{
    height:84vh;
}

.video-container{
    width:100vw;
    height:85.5vh !important;
    overflow:hidden;
    position:absolute;
  }
  .video-container iframe{
    position:absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 95%;
  }
  .video-container iframe {
    pointer-events: none;
  }
  .video-container iframe{
    position: absolute;
    top: -60px;
    left: 0;
    width: 100%;
    height: calc(100% + 120px);
  }
  .video-foreground{
    pointer-events:none;
  }
  .footer-container{
    padding-top: 1rem;
  }

  .foreground-text{
    font-size: 5rem;
    width: 100%;
    position:absolute;
    display: flex;
    align-content: center;
    justify-content: center;
   font-weight: 1200;
  }

  .foreground-text-subtitle{
    width: 100%;
    height: 60%;
    position:absolute;
    display: flex;
    align-content: center;
    justify-content: center;
  }

  .foreground-text-subtitle-2{
   padding-left: 0.5rem;
  }
.movie-carousel-container{
    width: 100vw;
    height: 100vh;
    background-color: black;
}

.movie-card{
background-repeat: no-repeat;
height:80vh;
width: 100vw;
background-size: cover;
background-image: linear-gradient(to bottom, rgba(0,0,0,0),rgba(0,0,0,1)) , var(--img) ;
}

.movie-card-container{
    height:90vh;
    background-color: black;
}

.movie-detail{
    display: flex;
    justify-content: space-between;
    grid-gap:3 rem;
    gap:3 rem;
    position: absolute;
    top:200px;
    left:4rem;
    width: 70%;
}
.movie-poster{
    height: 300px;
    border:1px solid gold;
    border-radius: 10px;
    overflow: hidden;
}
.movie-poster > img{
height:100%;
width: 100%;
}

.movie-title-card{
    font-size: 2rem;
    height:100%;
    align-self:center;
    justify-self:flex-start;
    color:white;
}

.trailer-button{
    font-size: 4rem;
    align-self:center;
}

.play-button-icon{
    color:gold;
    cursor: pointer;
    transition: 0.3s;
    -webkit-animation-iteration-count: 0;
            animation-iteration-count: 0;

}

.play-button-icon:hover {
    color:white;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    
}

.review-container{
    align-self: center;
}

.review-container:hover{
    -webkit-transform: scale(1.25);
            transform: scale(1.25);

}
